import React, { useEffect, useRef, useState } from "react";
import styles from "./DocList.module.css";
import ElemDeleteControl from "../ElemDeleteControlWrapper";
import QuickTools from "../../../ReusableComponents/QuickTools/QuickTools";
import { useDispatch } from "../../../Redux";
import axios from "../../../axios";
import {
  getGdToken,
  handleDisplayProperty,
  setMandatoryControls,
  updatePartialJSON
} from "../../../components/IFormDesignerLayout/IFormDesignerLogic";
import CustomizedAlert from "../../../ReusableComponents/Alert/CustomizedAlert";
import { GetState, GetStateFromData } from "../../../components/CustomHooks/CustomHooks";
import MandatoryMsg from "../../../ReusableComponents/ErrorMessageComponent/MandatoryMsg";
import secureLocalStorage from "react-secure-storage";
import cloneDeep from "lodash/cloneDeep";
import { updateStateInRedux } from "../../../Redux/Actions/FormStateActions";

const DocumentIntegration = (props) => {
  const isSubformInPage = props?.isSubformInPage || false;

  const contexts = GetStateFromData(props.containerId);
  const { tempCanWidth, controlSelected, doclistRerenderCount, mandatoryControls } = GetState(
    "tempCanWidth",
    "controlSelected",
    "doclistRerenderCount",
    "mandatoryControls"
  );

  const gdtoken = contexts.gdtoken;
  const witoken = contexts.witoken;

  const {
    deleteControl,
    formState2,
    device,
    transactionId,
    builderMode,
    formMataData,
    isMicroSerciceChecks,
    childComp,
    activePage,
    activeSubForm,
    dataObjectsContent,
    updateJson,
    openQuickToolFor
  } = contexts.contexts;

  const { updatedComponentInFormState } = updateJson;

  const conditionalQuickToolOpening = props.id === openQuickToolFor ? true : false;
  const themeData = formState2.theme;
  const controlThemeData = formState2.theme?.["DoclistStyle"];
  const previewmode = props.previewmode;
  const data = props.data;
  const style = data.style;

  const [open, setOpen] = useState(conditionalQuickToolOpening);
  const dispatch = useDispatch();
  const [mobileDisplay, setMobileDisplay] = useState(true);
  const [tabDisplay, setTabDisplay] = useState(true);
  const [docs, setDocs] = useState(props?.data?.value || []);
  const [filteredDocs, setFilteredDocs] = useState(props?.data?.value?.length ? props.data.value : []);
  const [openDoclistViewer, setOpenDoclistViewer] = useState(false);
  const [viewerData, setViewerData] = useState({});
  const [refresh, setRefresh] = useState(false);
  const [showMandatoryMsg, setShowMandatoryMsg] = useState(false);
  const [alertData, setAlertData] = useState({
    open: false,
    showConfirm: "showNotification"
  });

  const defaultStyle = {
    DocTypeStyle: "Style1",
    DocumentTypeLabelFontColor: "#000000",
    DocumentTypeLabelFontFamily: "Open Sans",
    DocumentTypeLabelFontWeight: "300",
    DocumentTypeLabelFontStyle: "Normal",
    DocumentTypeLabelFontSize: "14px",
    DocumentListLabelFontColor: "#000000",
    DocumentListLabelFontFamily: "Open Sans",
    DocumentListLabelFontSize: "14px",
    DocumentListLabelFontWeight: "300",
    DocumentListLabelFontStyle: "Normal",
    ButtonsType: "Secondary",
    DocumentButtonFontColor: "#0072C6",
    DocumentButtonFontFamily: "Open Sans",
    DocumentButtonFontSize: "14px",
    DocumentButtonFontWeight: "300",
    DocumentButtonFontStyle: "Normal",
    DocumentButtonBackgroundColor: "#FFFFFF",
    DocumentButtonCornerRadius: "2px",
    padding_left: "0px",
    padding_right: "0px",
    padding_top: "0px",
    padding_bottom: "0px"
  };

  const docListDefaultStyle = useRef(defaultStyle);

  //create a dummy state for render
  const [rerenderState, setReRenderState] = useState(false);

  const setDocListStyles = (newStyle) => {
    //store the prevState for comparison
    const prevState = JSON.stringify(docListDefaultStyle.current);
    docListDefaultStyle.current = { ...docListDefaultStyle.current, ...newStyle };

    if (!props.previewmode) {
      //check whether it's a designer mode or not
      //compare if some new changes has been made
      if (prevState !== JSON.stringify(docListDefaultStyle.current)) {
        //rerender on new changes
        setReRenderState(!rerenderState);
      }
    }
  };

  useEffect(() => {
    if (themeData?.["BasicTheme"]?.["FontColor"] && !controlThemeData?.["FontColor"]) {
      setDocListStyles({ color: themeData["BasicTheme"]["FontColor"] });
    }
    if (themeData?.["BasicTheme"]?.["FontFamily"] && !controlThemeData?.["FontFamily"]) {
      setDocListStyles({
        "font-family":
          themeData["BasicTheme"]["FontFamily"] === "Select" ? "Open Sans" : themeData["BasicTheme"]["FontFamily"]
      });
    }
    if (themeData?.["BasicTheme"]?.["FontSize"] && !controlThemeData?.["FontSize"]) {
      setDocListStyles({ "font-size": themeData["BasicTheme"]["FontSize"] });
    }
  }, [themeData?.["BasicTheme"]]);

  useEffect(() => {
    if (controlThemeData?.["DocTypeStyle"]) {
      setDocListStyles({ DocTypeStyle: controlThemeData["DocTypeStyle"] });
    }
    if (controlThemeData?.["DocumentListLabelFontColor"] && !style?.["DocumentListLabelFontColor"]) {
      setDocListStyles({ DocumentListLabelFontColor: controlThemeData["DocumentListLabelFontColor"] });
    }
    if (controlThemeData?.["DocumentListLabelFontFamily"] && !style?.["DocumentListLabelFontFamily"]) {
      setDocListStyles({ DocumentListLabelFontFamily: controlThemeData["DocumentListLabelFontFamily"] });
    }
    if (controlThemeData?.["DocumentListLabelFontSize"] && !style?.["DocumentListLabelFontSize"]) {
      setDocListStyles({ DocumentListLabelFontSize: controlThemeData["DocumentListLabelFontSize"] });
    }
    if (controlThemeData?.["DocumentListLabelFontWeight"] && !style?.["DocumentListLabelFontWeight"]) {
      setDocListStyles({ DocumentListLabelFontWeight: controlThemeData["DocumentListLabelFontWeight"] });
    }
    if (controlThemeData?.["DocumentListLabelFontStyle"] && !style?.["DocumentListLabelFontStyle"]) {
      setDocListStyles({ DocumentListLabelFontStyle: controlThemeData["DocumentListLabelFontStyle"] });
    }

    if (controlThemeData?.["DocumentTypeLabelFontColor"] && !style?.["DocumentTypeLabelFontColor"]) {
      setDocListStyles({ DocumentTypeLabelFontColor: controlThemeData["DocumentTypeLabelFontColor"] });
    }
    if (controlThemeData?.["DocumentTypeLabelFontFamily"] && !style?.["DocumentTypeLabelFontFamily"]) {
      setDocListStyles({ DocumentTypeLabelFontFamily: controlThemeData["DocumentTypeLabelFontFamily"] });
    }
    if (controlThemeData?.["DocumentTypeLabelFontSize"] && !style?.["DocumentTypeLabelFontSize"]) {
      setDocListStyles({ DocumentTypeLabelFontSize: controlThemeData["DocumentTypeLabelFontSize"] });
    }
    if (controlThemeData?.["DocumentTypeLabelFontWeight"] && !style?.["DocumentTypeLabelFontWeight"]) {
      setDocListStyles({ DocumentTypeLabelFontWeight: controlThemeData["DocumentTypeLabelFontWeight"] });
    }
    if (controlThemeData?.["DocumentTypeLabelFontStyle"] && !style?.["DocumentTypeLabelFontStyle"]) {
      setDocListStyles({ DocumentTypeLabelFontStyle: controlThemeData["DocumentTypeLabelFontStyle"] });
    }
  }, [controlThemeData]);

  useEffect(() => {
    if (style?.["DocTypeStyle"]) {
      setDocListStyles({ DocTypeStyle: style?.["DocTypeStyle"] });
    }

    if (style?.["DocumentListLabelFontColor"]) {
      setDocListStyles({ DocumentListLabelFontColor: style["DocumentListLabelFontColor"] });
    }
    if (style?.["DocumentListLabelFontFamily"]) {
      setDocListStyles({
        DocumentListLabelFontFamily:
          style["DocumentListLabelFontFamily"] === "Select"
            ? controlThemeData?.["DocumentListLabelFontFamily"] || defaultStyle.DocumentListLabelFontFamily
            : style["DocumentListLabelFontFamily"]
      });
    }
    if (style?.["DocumentListLabelFontSize"]) {
      setDocListStyles({
        DocumentListLabelFontSize:
          style["DocumentListLabelFontSize"] === "Select"
            ? controlThemeData?.["DocumentListLabelFontSize"] || defaultStyle.DocumentListLabelFontSize
            : style["DocumentListLabelFontSize"]
      });
    }
    if (style?.["DocumentListLabelFontWeight"]) {
      setDocListStyles({
        DocumentListLabelFontWeight:
          style["DocumentListLabelFontWeight"] === "Select"
            ? controlThemeData?.["DocumentListLabelFontWeight"] || defaultStyle.DocumentListLabelFontWeight
            : style["DocumentListLabelFontWeight"]
      });
    }
    if (style?.["DocumentListLabelFontStyle"]) {
      setDocListStyles({
        DocumentListLabelFontStyle:
          style["DocumentListLabelFontStyle"] === "Select"
            ? controlThemeData?.["DocumentListLabelFontStyle"] || defaultStyle.DocumentListLabelFontStyle
            : style["DocumentListLabelFontStyle"]
      });
    }

    if (style?.["DocumentTypeLabelFontColor"]) {
      setDocListStyles({ DocumentTypeLabelFontColor: style["DocumentTypeLabelFontColor"] });
    }
    if (style?.["DocumentTypeLabelFontFamily"]) {
      setDocListStyles({
        DocumentTypeLabelFontFamily:
          style["DocumentTypeLabelFontFamily"] === "Select"
            ? controlThemeData?.["DocumentTypeLabelFontFamily"] || defaultStyle.DocumentTypeLabelFontFamily
            : style["DocumentTypeLabelFontFamily"]
      });
    }
    if (style?.["DocumentTypeLabelFontSize"]) {
      setDocListStyles({
        DocumentTypeLabelFontSize:
          style["DocumentTypeLabelFontSize"] === "Select"
            ? controlThemeData?.["DocumentTypeLabelFontSize"] || defaultStyle.DocumentTypeLabelFontSize
            : style["DocumentTypeLabelFontSize"]
      });
    }
    if (style?.["DocumentTypeLabelFontWeight"]) {
      setDocListStyles({
        DocumentTypeLabelFontWeight:
          style["DocumentTypeLabelFontWeight"] === "Select"
            ? controlThemeData?.["DocumentTypeLabelFontWeight"] || defaultStyle.DocumentTypeLabelFontWeight
            : style["DocumentTypeLabelFontWeight"]
      });
    }
    if (style?.["DocumentTypeLabelFontStyle"]) {
      setDocListStyles({
        DocumentTypeLabelFontStyle:
          style["DocumentTypeLabelFontStyle"] === "Select"
            ? controlThemeData?.["DocumentTypeLabelFontStyle"] || defaultStyle.DocumentTypeLabelFontStyle
            : style["DocumentTypeLabelFontStyle"]
      });
    }

    if (style?.["ButtonsType"]) {
      setDocListStyles({ ButtonsType: style["ButtonsType"] });
    }
    if (style?.["DocumentButtonFontColor"]) {
      setDocListStyles({ DocumentButtonFontColor: style["DocumentButtonFontColor"] });
    } else if (style?.["ButtonsType"] && style?.["ButtonsType"] === "Primary") {
      setDocListStyles({ DocumentButtonFontColor: "#fff" });
    } else if (style?.["ButtonsType"] && style?.["ButtonsType"] === "Secondary") {
      setDocListStyles({ DocumentButtonFontColor: "#2067ED" });
    } else if (style?.["ButtonsType"] && style?.["ButtonsType"] === "Text Button") {
      setDocListStyles({ DocumentButtonFontColor: "#2067ED" });
    }
    if (style?.["DocumentButtonFontFamily"]) {
      setDocListStyles({
        DocumentButtonFontFamily:
          style["DocumentButtonFontFamily"] === "Select"
            ? controlThemeData?.["DocumentButtonFontFamily"] || defaultStyle.DocumentButtonFontFamily
            : style["DocumentButtonFontFamily"]
      });
    }
    if (style?.["BulkUploadButton"]) {
      setDocListStyles({ BulkUploadButton: style["BulkUploadButton"] === "true" });
    }
    if (style?.["DocumentButtonFontSize"]) {
      setDocListStyles({
        DocumentButtonFontSize:
          style["DocumentButtonFontSize"] === "Select"
            ? controlThemeData?.["DocumentButtonFontSize"] || defaultStyle.DocumentButtonFontSize
            : style["DocumentButtonFontSize"]
      });
    }
    if (style?.["DocumentButtonFontWeight"]) {
      setDocListStyles({
        DocumentButtonFontWeight:
          style["DocumentButtonFontWeight"] === "Select"
            ? controlThemeData?.["DocumentButtonFontWeight"] || defaultStyle.DocumentButtonFontWeight
            : style["DocumentButtonFontWeight"]
      });
    }
    if (style?.["DocumentButtonFontStyle"]) {
      setDocListStyles({
        DocumentButtonFontStyle:
          style["DocumentButtonFontStyle"] === "Select"
            ? controlThemeData?.["DocumentButtonFontStyle"] || defaultStyle.DocumentButtonFontStyle
            : style["DocumentButtonFontStyle"]
      });
    }
    if (style?.["DocumentButtonBackgroundColor"]) {
      setDocListStyles({ DocumentButtonBackgroundColor: style["DocumentButtonBackgroundColor"] });
    } else if (style?.["ButtonsType"] && style?.["ButtonsType"] === "Primary") {
      setDocListStyles({ DocumentButtonBackgroundColor: "#2067ED" });
    } else if (style?.["ButtonsType"] && style?.["ButtonsType"] === "Secondary") {
      setDocListStyles({ DocumentButtonBackgroundColor: "#fff" });
    } else if (style?.["ButtonsType"] && style?.["ButtonsType"] === "Text Button") {
      setDocListStyles({ DocumentButtonBackgroundColor: "#fff" });
    }
    if (style?.["DocumentButtonCornerRadius"]) {
      setDocListStyles({
        DocumentButtonCornerRadius:
          style["DocumentButtonCornerRadius"] === "Select"
            ? controlThemeData?.["DocumentButtonCornerRadius"] || defaultStyle.DocumentButtonCornerRadius
            : style["DocumentButtonCornerRadius"]
      });
    }

    if (style?.["SpacingLeft"]) {
      setDocListStyles({ padding_left: style?.["SpacingLeft"] });
    }
    if (style?.["SpacingRight"]) {
      setDocListStyles({ padding_right: style?.["SpacingRight"] });
    }
    if (style?.["SpacingTop"]) {
      setDocListStyles({ padding_top: style?.["SpacingTop"] });
    }
    if (style?.["SpacingBottom"]) {
      setDocListStyles({ padding_bottom: style?.["SpacingBottom"] });
    }
  }, [cloneDeep(style)]);

  const inputStyleGroup = {
    paddingLeft: docListDefaultStyle.current.padding_left,
    paddingRight: docListDefaultStyle.current.padding_right,
    paddingTop: docListDefaultStyle.current.padding_top,
    paddingBottom: docListDefaultStyle.current.padding_bottom,
    display: "block",
    height: "100%",
    border: showMandatoryMsg ? "1px solid red" : "unset"
  };

  const secondaryDivStyle = {
    width: "calc(100% - 2px)",
    height: "calc(100% - 2px)"
  };
  const startMobile = useRef();
  const endMobile = useRef();
  const startTab = useRef();
  const endTab = useRef();

  useEffect(() => {
    if (formState2?.style) {
      startMobile.current = parseInt(formState2.style?.["Breakpoint"]?.["Mobile"]?.["StartValue"]);
      endMobile.current = parseInt(formState2.style?.["Breakpoint"]?.["Mobile"]?.["EndValue"]);
      startTab.current = parseInt(formState2.style?.["Breakpoint"]?.["Tab"]?.["StartValue"]);
      endTab.current = parseInt(formState2.style?.["Breakpoint"]?.["Tab"]?.["EndValue"]);
    }
  }, [
    formState2.style?.["Breakpoint"],
    formState2.style?.["Breakpoint"]?.["Mobile"],
    formState2.style?.["Breakpoint"]?.["Tab"]
  ]);

  useEffect(() => {
    if (props?.previewmode)
      handleDisplayProperty(
        props,
        device,
        setMobileDisplay,
        setTabDisplay,
        startMobile.current,
        endMobile.current,
        startTab.current,
        endTab.current
      );
  }, [
    props?.data?.style?.["MobileVisible"],
    props?.data?.style?.["TabsVisible"],
    device,
    endMobile,
    endTab,
    startMobile,
    startTab,
    tempCanWidth
  ]);

  const setMappingFields = () => {
    props.data["mappingJson"] = {
      DocType: "doctype",
      DocName: "docname",
      DocSize: "docsize",
      ResourceType: "resourcetype",
      DocComment: "doccomment",
      DocIndex: "docindex",
      NoOfPageInDoc: "noofpageindoc",
      DocControlID: "doccontrolid",
      Latitude: "latitude",
      Longitude: "longitude"
    };
    updatedComponentInFormState(props.data.id, props.data);
  };

  useEffect(() => {
    if (
      !props.data?.["mappingJson"] &&
      (childComp === "application" || activePage || activeSubForm) &&
      !props.previewmode
    ) {
      setMappingFields();
    }
  }, []);

  useEffect(() => {
    if (props?.data?.value) {
      setDocs([...props.data.value]);
    }
  }, []);

  useEffect(() => {
    if (props?.data && props.previewmode) {
      props.data["value"] = [...filteredDocs];
      updatedComponentInFormState(props?.data?.id, props?.data);
    }
  }, [filteredDocs]);

  /* --- additonal data object fields handling STARTS --- */
  const parameter_arr_Default = [
    { label: "Doc Type", value: "DocType" },
    { label: "Doc Name", value: "DocName" },
    { label: "Doc Size", value: "DocSize" },
    { label: "Doc Comment", value: "DocComment" },
    { label: "Doc Resource Type", value: "ResourceType" },
    { label: "Doc Index", value: "DocIndex" },
    { label: "No of pages", value: "NoOfPageInDoc" },
    { label: "Doc Control ID", value: "DocControlID" },
    { label: "Latitude", value: "Latitude" },
    { label: "Longitude", value: "Longitude" }
  ];

  async function recursionMapping(contentData) {
    Object.values?.(contentData).map((content) => {
      if (content?.children && content["children"] && content["VariableName"] !== props.data.dataclass) {
        recursionMapping(content["children"]);
      }
      if (
        content["treeName"] === props.data.dataclass ||
        (content["children"] && content["VariableName"] === props.data.dataclass)
      ) {
        const temp = [];
        Object.values(content["children"]).map((columnContent) => {
          if (
            !parameter_arr_Default.some(
              (param) => param?.value?.toLowerCase() === columnContent?.VariableName?.toLowerCase()
            ) &&
            columnContent.VariableName !== "referencekey" &&
            columnContent.VariableName !== "insertionorderid"
          ) {
            temp.push(columnContent);
          }
        });

        props.data["additionalFields"] = temp;
        updatePartialJSON(props.data.id, props.data);
      }
    });
  }

  useEffect(() => {
    if (props?.data?.dataclass) {
      recursionMapping(dataObjectsContent);
    }
  }, []);

  /* --- additonal data object fields handling ENDS --- */

  async function setFilteredDataforDocs(openedFromUseEffect) {
    if (window?.loadMicroFrontend && window?.MicroAppsMap && Object.values(window.MicroAppsMap).length > 0) {
      const filtered_Docs = [];
      const isPortal = builderMode === "application";
      const widpid = isPortal
        ? ""
        : `pid=${contexts?.generalData?.processinstanceid || window?.formBuilder_pid_generalData || ""}&wid=${
            formMataData?.wid || "1"
          }&`;

      docs?.map((obj) => {
        const doc_type = typeof obj.doc_type === "string" ? JSON.parse(obj.doc_type || null) : obj.doc_type;
        const docTypee = obj?.type || obj?.resource_type;
        const ImgIndex = obj?.i_s_index?.includes("#")
          ? obj?.i_s_index.split("#")[0] || doc_type?.i_s_index?.split("#")[0]
          : doc_type?.i_s_index?.split("#")[0];
        const VolIndex = obj?.i_s_index?.includes("#")
          ? obj?.i_s_index.split("#")[1] || doc_type?.i_s_index?.split("#")[1]
          : doc_type?.i_s_index?.split("#")[1];

        let doc_size = "";
        if (obj.size && !isNaN(obj.size)) {
          doc_size = (obj.size / 1024).toFixed(2).toString() + " KB";
        }

        const url_info = {
          get_stamp: "/wd-rest/app/stampServlet",
          get_stamp_ini: "/wd-rest/app/getdocstamps",
          get_stamp_annotation: "/wd-rest/app/viewimagestamp",
          get_annotation: "/wd-rest/app/viewimageannotation",
          save_annotation: "/wd-rest/app/imageannotation",
          // Paramters to be passed as query string in get_document, as it is required by Opall viewer.
          get_document: `/wd-rest/app/getdocuments?ImgIndex=${ImgIndex}&VolIndex=${VolIndex}&DocExt=${
            obj?.extension
          }&DocIndex=${obj?.id}&PageNo=1&docType=${docTypee}&DocSize=${
            obj?.size
          }&ArchivalMode=N&ArchivalCabinet=&${widpid}taskid=&DocumentName=${encodeURIComponent(obj?.name)}`
        };

        const newObj = {
          ...obj,
          doc_type: doc_type,
          url_info: url_info,
          size: doc_size || "",
          ...(doc_type.i_s_index ? { i_s_index: doc_type.i_s_index } : {})
        };

        filtered_Docs.push(newObj);

        const uploadedDocName = doc_type?.name || "";

        const curIndex = props?.data?.json?.["available_doc_types"]?.findIndex((item) => item.name === uploadedDocName);
        let foundDocJson = props?.data?.json?.["available_doc_types"]?.[curIndex];

        if (foundDocJson?.mandatory) {
          foundDocJson = {
            ...foundDocJson,
            docName: uploadedDocName || "",
            docManid: [...(foundDocJson.docManid || []), isPortal ? obj.i_s_index : obj.id]
          };

          foundDocJson["isUploaded"] = !!foundDocJson.docManid.length;
          props.data.json["available_doc_types"][curIndex] = foundDocJson;
        }
      });

      updatedComponentInFormState(props?.data?.id, props?.data);
      if (filtered_Docs?.length) {
        setFilteredDocs([...filtered_Docs]);
      } else {
        setFilteredDocs([...filteredDocs]);
      }
      const new_documentData = { ...props.data.json, documents: filtered_Docs?.length ? filtered_Docs : filteredDocs };
      loadDoc(new_documentData);
      const returnData = [...filtered_Docs];
      return returnData;
    }
  }

  useEffect(() => {
    if (props.previewmode) {
      setFilteredDataforDocs(true);
    }
  }, [docs, document.querySelector("script[src='/integration/integration.js']")]);

  async function callBack_func(type, payload, response) {
    if (type === "upload") {
      if (props?.data?.style?.Mandatory === "true") {
        props.data.isUploaded = true;
        removeIdFromMandatoryControl("uploaded", props.data.id);
      }

      const documentsArray = [];
      const isPortal = builderMode === "application";
      const widpid = isPortal
        ? ""
        : `pid=${contexts?.generalData?.processinstanceid || window?.formBuilder_pid_generalData || ""}&wid=${
            formMataData?.wid || "1"
          }&`;

      response?.data?.response_list.map((doc, indexOfDocs) => {
        let doc_size = "";
        if (doc.size && !isNaN(doc.size)) {
          doc_size = (doc.size / 1024).toFixed(2).toString() + " KB";
        }

        const json = {
          docType: JSON.stringify({ ...(doc?.doc_type || {}), i_s_index: doc?.i_s_index }) || "",
          docName: doc?.name || "",
          docSize: doc.size || "",
          docComment: doc?.message || "",
          docImageIndex: (isPortal ? doc?.i_s_index : doc?.id) || "",
          noOfPageInDoc: doc?.noOfPages || "1",
          latitude: doc?.latitude || "",
          longitude: doc?.longitude || "",
          resourceType: doc?.type,
          // "transactionid":transactionId,
          gdtoken: gdtoken,
          witoken: witoken,
          controlId: props.data.id,
          additionalFields: doc?.additionalFields || []
        };

        axios
          .post("/viewform/uploadDoc", json, { withCredentials: true })
          .then(() => {
            setAlertData({
              open: true,
              showConfirm: "showNotification",
              alertType: "success",
              vertical: "bottom",
              horizontal: "left",
              alertContentMsg: "UPLOAD_SUCCESSFULLY"
            });
          })
          .catch((err) => console.log(err));

        const ImgIndex = doc?.i_s_index?.split("#")?.[0];
        const VolIndex = doc?.i_s_index?.split("#")?.[1];

        const jsonForDocumentsArray = {
          name: doc?.name || "",
          id: (isPortal ? doc?.i_s_index : doc?.id) || "",
          version: "",
          no_of_pages: doc?.noOfPages || "1",
          doc_type: doc?.doc_type || {},
          owner: "",
          size: doc_size || "",
          last_modified_on: "",
          comments: doc?.message,
          pinned: false,
          enable_delete: true,
          enable_version: false,
          enable_download: true,
          checkin_status: "",
          is_conversation: false,
          i_s_index: doc?.i_s_index || "",
          org_name: "",
          thumbnail: "",
          extension: doc?.extension || "",
          additionalFields: doc?.additionalFields || [],
          resource_type: doc?.type,
          url_info: {
            get_stamp: "/wd-rest/app/stampServlet",
            get_stamp_ini: "/wd-rest/app/getdocstamps",
            get_stamp_annotation: "/wd-rest/app/viewimagestamp",
            get_annotation: "/wd-rest/app/viewimageannotation",
            save_annotation: "/wd-rest/app/imageannotation",
            // Paramters to be passed as query string in get_document, as it is required by Opall viewer.
            get_document: `/wd-rest/app/getdocuments?ImgIndex=${ImgIndex}&VolIndex=${VolIndex}&DocExt=${
              doc?.extension
            }&DocIndex=${doc?.id}&PageNo=1&docType=${doc?.type}&DocSize=${
              doc?.size
            }&ArchivalMode=N&ArchivalCabinet=&${widpid}taskid=&DocumentName=${encodeURIComponent(doc?.name)}`
          }
        };

        documentsArray.push(jsonForDocumentsArray);

        if (response?.data?.response_list.length !== 0 && response?.data?.response_list.length === indexOfDocs + 1) {
          setFilteredDocs((prev) => [...prev, ...documentsArray]);
        }
      });

      const uploadedDocName = response?.data?.response_list?.[0]?.doc_type?.name || "";

      const curIndex = props?.data?.json?.["available_doc_types"]?.findIndex((item) => item.name === uploadedDocName);
      let foundDocJson = props?.data?.json?.["available_doc_types"]?.[curIndex];

      if (foundDocJson?.mandatory) {
        removeIdFromMandatoryControl("uploaded", `${uploadedDocName}_${props?.data?.id}`);

        foundDocJson = {
          ...foundDocJson,
          docName: uploadedDocName || "",
          docManid: response?.data?.response_list?.map((item) => (isPortal ? item?.i_s_index : item?.id)) || ""
        };

        foundDocJson["isUploaded"] = !!foundDocJson.docManid.length;
        props.data.json["available_doc_types"][curIndex] = foundDocJson;
      }
      updatedComponentInFormState(props?.data?.id, props?.data);
      if (window.documentPostHook) {
        window.documentPostHook(props?.data?.id, {
          [uploadedDocName]: response?.data?.response_list?.map((item) => (isPortal ? item?.i_s_index : item?.id)) || ""
        });
      }
    } else if (type === "onDocClick") {
      const isPortal = builderMode === "application";

      const docData = {
        available_doc_types: props.data?.json?.available_doc_types,
        upload_doc_as: props.data?.json?.upload_doc_as,
        documents: filteredDocs?.length ? filteredDocs : [],
        payload: {
          ...payload,
          docviewer_info: {
            viewer_location: "inline", //inline, popup, window
            ContainerId: "rdDIv", //Optional, Required in case viewer_location is Inline //Optional, Required in case viewer_location is Inline,
            view_annotation: true,
            view_stamps: true,
            show_opall_menu: true,
            opall_params: {
              printOption: "false",
              TransformOption: "",
              AllPages: "true"
            }
          },
          transactionid: transactionId || ""
        },
        transactionid: transactionId || "",
        dms_type: isPortal ? "3" : payload?.id?.includes("#") ? "3": "1",
        ...(isPortal
          ? {
              isMicroServiceMode: isMicroSerciceChecks.IsMicroserviceMode || "N",
              ...(isMicroSerciceChecks.IsMicroserviceMode === "Y"
                ? {
                    userIndex: isMicroSerciceChecks.MicroAuthParam,
                    userDBId: isMicroSerciceChecks.MicroAuthToken
                  }
                : {})
            }
          : {}),
        isPortal
      };
      setViewerData({ ...docData });

      setOpenDoclistViewer(true);
    } else if (type === "delete") {
      deleteDoc(response.data.response_list);
    } else if (type === "viewType") {
      if (props?.data?.json) {
        props.data.json["viewType"] = payload;
        updatedComponentInFormState(props?.data?.id, props?.data);
      }
    }
  }

  const deleteDoc = (responseList = []) => {
    const json = { controlId: props?.data?.id || props?.id, docImageIndex: "", gdtoken: gdtoken, witoken: witoken };
    const docIndexesMap = responseList.map((item) => (item.status === "Success" ? item.doc_index : ""));
    const docIndexes = docIndexesMap.filter((item) => item !== "");
    json.docImageIndex = docIndexes.join(",");

    if (docIndexes.length) {
      axios
        .post("/viewform/deleteDoc", json, { withCredentials: true })
        .then(() => {
          const isPortal = builderMode === "application";
          const updatedDocs = filteredDocs.filter(
            (item) => !docIndexes.includes(isPortal ? item?.i_s_index : item?.id)
          );
          setFilteredDocs(() => [...updatedDocs]);
          const docCount = [];

          props?.data?.json?.["available_doc_types"]?.forEach((item) => {
            item.docManid = item?.docManid?.filter((item2) => !docIndexes.includes(item2));
            if (!item?.docManid?.length && item?.mandatory) {
              item.isUploaded = false;
            }
            if (props?.data?.style?.Mandatory === "true") {
              docCount.push(item?.docManid?.length);
            }
          });

          if (docCount.every((item) => item === 0)) {
            props.data.isUploaded = false;
          } else {
            props.data.isUploaded = true;
          }

          setAlertData({
            open: true,
            showConfirm: "showNotification",
            alertType: "success",
            vertical: "bottom",
            horizontal: "left",
            alertContentMsg: "Document Deleted Successfully"
          });
        })
        .catch((err) => console.log(err));
    }
  };

  useEffect(() => {
    if (openDoclistViewer === true) {
      window.openDocumentViewer("", "ltr", viewerData.payload, viewerData, "doclistViewer_id_form");
    }
  }, [openDoclistViewer]);

  const [loadedOnce, setLoadedOnce] = useState(false);

  function loadDoc(documentData) {
    if (
      !props.data?.value ||
      props.data?.value?.length === 0 ||
      (props?.data?.value?.length > 0 && filteredDocs?.length > 0)
    ) {
      const isPortal = builderMode === "application";
      if(documentData?.documents?.length>0){
        documentData?.documents?.map((doc,index)=>{        
          documentData.documents[index]["dms_type"]=isPortal ? "3" : doc?.id?.includes("#") ? "3": "1"        
        })
      }
      const jwtToken =
        JSON.parse(
          secureLocalStorage.getItem(
            window.isPortalLive ? `launchpadKey_${window.applicationName}` || "launchpadKey" : "launchpadKey"
          )
        )?.token ||
        secureLocalStorage.getItem("Authorization") ||
        undefined;

      const available_doc_types = documentData?.available_doc_types?.filter((doc) => !doc.hideDocType);
      let enable_upload = props?.data?.style?.Enable === "true" || props?.data?.style?.Enable === true;
      const viewType = documentData?.viewType || "table";

      if (!props?.previewmode || !getGdToken()) {
        enable_upload = false;
      }

      const translationStyles1 =
        window?.appformBuilder_configs?.selectedLanguage === "ar" ||
        window?.appformBuilder_configs?.selectedLanguage === "ar-SA";

      const propsData = {
        Module: "WCL",
        Component: "DocumentListViewer",
        InFrame: false,
        ContainerId: props.data.id,
        locale: window?.appformBuilder_configs?.selectedLanguage,
        direction: translationStyles1 ? "rtl" : "ltr",
        data: {
          ...{ ...documentData, available_doc_types, enable_upload },
          dms_type: isPortal ? "3" : "1",
          ...(isPortal
            ? {}
            : {
                pid: contexts?.generalData?.processinstanceid || window?.formBuilder_pid_generalData || "",
                wid: formMataData?.wid || "1"
              })
        },
        passedData: {
          ...{ ...documentData, available_doc_types, enable_upload, viewType },
          ...(isPortal
            ? {
                isMicroServiceMode: isMicroSerciceChecks.IsMicroserviceMode || "N",
                ...(isMicroSerciceChecks.IsMicroserviceMode === "Y"
                  ? {
                      userIndex: isMicroSerciceChecks.MicroAuthParam,
                      userDBId: isMicroSerciceChecks.MicroAuthToken,
                      cabinetName: isMicroSerciceChecks.cabinetName
                    }
                  : {})
              }
            : {
                pid: contexts?.generalData?.processinstanceid || window?.formBuilder_pid_generalData || "",
                wid: formMataData?.wid || "1"
              }),
          formJson: docListDefaultStyle?.current || {},
          DocumentLabel: props?.data?.style?.DocumentLabel || "",
          DocumentDescription: props?.data?.style?.DocumentDescription || "",
          isDocumentMandatory: props?.data?.style?.Mandatory === "true",
          additionalFields: props?.data?.["additionalFields"] || [],
          isPortal,
          token: jwtToken
        },
        Callback: callBack_func
      };
      if (window.loadDoclist) {
        window.loadDoclist(propsData);
      }
      setLoadedOnce(true);
    }
  }

  useEffect(() => {
    window.setTimeout(() => {
      const isPortal = builderMode === "application";

      if (!loadedOnce && !isPortal && !formMataData) {
        loadDoc({ ...props.data.json, documents: filteredDocs });
      }
    }, 3000);
  }, []);

  useEffect(() => {
    window.setTimeout(() => {
      const doclistContainer = document.getElementById(props?.data?.id);

      if (doclistContainer && doclistContainer.childNodes.length === 0) {
        loadDoc({ ...props.data.json, documents: filteredDocs });
      }
    }, 2000);
  }, []);

  useEffect(() => {
    if (!window?.loadMicroFrontend) {
      window.loadDoclistComponent(loadDoc, { ...props.data.json, documents: filteredDocs || [] });
      setRefresh(!refresh);
    }
  }, []);

  useEffect(() => {
    if (
      (window?.loadMicroFrontend && window?.MicroAppsMap && Object.values(window.MicroAppsMap).length > 0) ||
      !document.querySelector("#appFormBuilder_root_not_mf")
    ) {
      loadDoc({ ...props.data.json, documents: filteredDocs });
    }
  }, [props.data.json, refresh, filteredDocs, doclistRerenderCount, props?.data?.style?.updateFormState]);

  const doclistViewer_header_style = {
    padding: "2px 8px",
    backgroundColor: "#f2f2f2",
    fontWeight: "bold",
    display: "flex",
    justifyContent: "end"
  };

  const doclistViewer_span_style = { cursor: "pointer" };
  const doclistViewer_form_style = { height: "100%" };

  function removeIdFromMandatoryControl(value, docId) {
    if (mandatoryControls.includes(docId)) {
      if (value.length > 0) {
        const newMandatoryList = mandatoryControls.filter((item) => item !== docId);
        setMandatoryControls([...newMandatoryList]);
        dispatch(updateStateInRedux({ name: "doclistRerenderCount", value: new Date(), key: props.containerId }));
      }
    }
  }

  useEffect(() => {
    if (props.previewmode) {
      if (mandatoryControls.includes(props.data?.id)) {
        setShowMandatoryMsg(true);
      } else {
        setShowMandatoryMsg(false);
      }

      if (props?.data?.json?.["available_doc_types"]?.length) {
        props.data.json["available_doc_types"].forEach((docItem) => {
          if (mandatoryControls.includes(`${docItem?.name}_${props?.data?.id}`) && docItem?.mandatory) {
            docItem.displayDocMandatoryError = !docItem.isUploaded;
          }
        });
      }
    }
  }, [mandatoryControls]);

  useEffect(() => {
    if (props?.previewmode && document.getElementById(props.data.id + "_parent")) {
      document.getElementById(props.data.id + "_parent").ondragstart = () => {
        return false;
      };
    }
  }, [props?.previewmode]);

  if (previewmode) {
    if (
      document.getElementById(props.id) &&
      document.getElementById(props.id).childNodes &&
      document?.getElementById(props.id).childNodes[0]
    ) {
      document.getElementById(props.id).childNodes[0].style.height = "";
    }

    return (
      <>
        {props?.data?.style?.["Visible"] === "true" ? (
          <div
            id={props.data.id + "_parent"}
            control_id={props.data.id}
            frameIndex={data.frameIndex}
            elementIndex={data.elementIndex}
            sheetId={props.sheetId}
            style={mobileDisplay === false || tabDisplay === false ? { display: "none" } : inputStyleGroup}
          >
            <div id={props.data.id} style={{ ...secondaryDivStyle, borderColor: showMandatoryMsg ? "red" : "" }}></div>

            {showMandatoryMsg ? (
              <MandatoryMsg id={props.data.id} mandatoryMsg={props.data?.style?.ValidationMessage} />
            ) : null}

            {openDoclistViewer && (
              <>
                <div onClick={() => setOpenDoclistViewer(false)} className={styles.shade_main}></div>
                <div className={styles.doclistViewer_popup}>
                  <div style={doclistViewer_header_style}>
                    <span style={doclistViewer_span_style} onClick={() => setOpenDoclistViewer(false)}>
                      X
                    </span>
                  </div>
                  <div id="doclistViewer_id_form" style={doclistViewer_form_style}></div>
                </div>
              </>
            )}
          </div>
        ) : null}
        {alertData.open === true && <CustomizedAlert alertData={alertData} />}
      </>
    );
  } else {
    return (
      <>
        <div
          control_id={props.data.id}
          frameIndex={data.frameIndex}
          elementIndex={data.elementIndex}
          sheetId={props.sheetId}
          style={{ ...inputStyleGroup, position: "relative", cursor: "default" }}
          onClick={isSubformInPage ? () => {} : (e) => props.handleSelected(e, props.data.id, "n")}
          onKeyDown={(e) => props.handleSelected(e)}
          tabIndex="1"
        >
          <div id={props.data.id} onKeyDown={(e) => props.handleSelected(e)} style={{ ...secondaryDivStyle, borderColor: showMandatoryMsg ? "red" : "" }}></div>

          {openDoclistViewer && <div id="doclistViewer_id_form"></div>}
          {controlSelected?.id === props?.id && (
            <ElemDeleteControl
              isSubformInPage={isSubformInPage}
              containerId={props.containerId}
              id={props.data.id}
              deleteControl={deleteControl}
              elemType={props.data.controlName}
              setDragging={props.setDragging}
              handleReorderStart={props.handleReorderStart}
              handleReorderDrop={props.handleReorderDrop}
              data={props.data}
              dragId={props.dragId}
              dragType={"column"}
            />
          )}
          {window?.openQuickTools && open && (
            <QuickTools
              containerId={props.containerId}
              selectedControl={{ ...props.data, elem_type: props.data.controlName }}
              open={open}
              setOpen={setOpen}
            />
          )}
        </div>
        {alertData.open === true && <CustomizedAlert alertData={alertData} />}
      </>
    );
  }
};

export default DocumentIntegration;
